.App {
  text-align: center;
  font-family: "Helvetica Rounded";
  width: 100%;
  height: 100%;

}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}


:root {
  --main-bg-color: #245baa;
  --main-grey-color: #c8c8c8;
  --second-grey-color: #959595;
}
@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}


.page_header {
  position: absolute;
  height: 10%;
  width: 100%;
  top: 0%;
  left: 0%;
}

.site_logo {
  height: 100%;
  float: left;
  margin: 20px;
  cursor: pointer;
}

.site_logo:hover{
  filter: brightness(1.4);

}


.site_logo img {
  width: 100%;
}

.selector {
  position: absolute;
  right: 0%;
  top: 5%;
  width: 25%;
  height: 30%;
}

#selector_icon {
  height: 100%;
  float: right;
  z-index: 1;
  position: relative;
}


.content {
  display: flex;
  height: 100%;
  justify-content: space-between;
  flex-direction: column;
  width: 200px;
}
.box {
  height: 50px;
  display: flex;
  align-items: center; /* Vertical center alignment */
  justify-content: right; /* Horizontal center alignment */
  color: var(--main-grey-color);
  cursor: pointer;
  font-size: 20pt;
}
.picked {
  color: #245baa;
}
.box:hover{
  color: black;
}

.slider_container {
  position: relative;
  width: fit-content;
  height: 100%;
}
.slider_amount{
  position: absolute;
  background-color: var(--main-bg-color);
  height: 0%;
  width: 100%;
  left: 0%;
  top: 0%;
  z-index: 0;
}
.slider_amount_base{
  position: absolute;
  background-color: var(--main-grey-color);
  height: 100%;
  width: 100%;
  left: 0%;
  top: 0%;
  z-index: -1;
}
.row {
  display: flex;
}

.column {
  flex: 50%;
}

#home_logo{
  width: 60%;
  top: 40%;
  position: absolute;
  left: 50%;
  transform: translate(-50%, -50%);
}

#start_button {
  width: 200px;
  top: 80%;
  position: absolute;
  left: 50%;
  transform: translate(-50%, -50%);
  cursor: pointer;
}


.start_page {
  background-color: white;
  z-index: 5;
  width: 100%;
  height: 100%;
  position: relative;
}

.hidden {
  opacity: 0;
  display: none;
}
#start_button:hover {
  filter: brightness(1.4);
}

.question_container {
  position: absolute;
  width: 30%;
  height: 50%;
  right: 10%;
  top: 35%;
  text-align: left;
}


.answer_box {
  padding: 20px;
  color: var(--main-grey-color);
  border: 1px solid var(--main-grey-color);
  margin-top: 10px;
  cursor: pointer;
  border-radius: 10px;
  font-size: 20pt;
}

.answer_box:hover {
  color: var(--main-bg-color);
  border: 1px solid var(--second-grey-color);

}


h1 {
  font-size: 90pt;
  padding: 0px;
  margin: 0px;
}
h2 {
  margin: 0px;
  font-size: 45pt;
}

.selected {
  color: white;
  background-color: var(--main-bg-color);
}

.selected:hover {
  color: black;
}


#guy {
  position: absolute;
  left: 10%;
  bottom: 5%;
}

.steps video {
  width: 60%;
  height: 100%;
  z-index: -5;
  object-fit: contain;
  position: absolute;
  left: 0;
  top: 0;
}
.steps {
  height: 100%;
}
.next_button {
  position: absolute;
  right: 2%;
  bottom: 2%;
  width: fit-content;
  font-size: 20pt;
  color: var(--main-bg-color);
  border: 1px solid var(--main-bg-color);
  padding: 20px;
  border-radius: 20px;
  cursor: pointer;
}

.next_button:hover{
  background-color: var(--main-bg-color);
  color: white;
}

#back_button {
  left: 2%;
}

.full_screen {
}
.full_screen img {
  width: 100%;
  height: 100%;
}

.hand video {
  width: 500px;
  margin-top: 500px;
  position: relative;
  pointer-events: none;
}

.hand {
  pointer-events: none;
  
}

.crowd {
  width: 50%;
  bottom: 20%;
  position: absolute;
}
.crowd .guy_position {
  width: 20%;
  height: 200px;
}
.crowd video {
  cursor: pointer;
  width: 25%;
}
.crowd video:hover {
  filter: brightness(1.5);
  filter: hue-rotate(90deg)
}
.guy {
  width: fit-content;
  position: relative;
}

#guy_no_eyes {
  width: 100%;
}
.guy_position{
  position: relative;
  display: inline-block
}

.eye {
  position: absolute;
  left: 50%;
  top: 16%;
}


#head {
  position: absolute;
  width: 50px;
  top: 20%;
  left: 50%;
  height: 50px;
}

.twiddle {
  width: 10%;
}

.full_screen {
  cursor: none;
}

.disabled {
  color: var(--second-grey-color);
  border: 1px solid var(--second-grey-color);
  cursor:crosshair;
}

.disabled:hover {
  color: var(--second-grey-color);
  border: 1px solid var(--second-grey-color);
  background-color: white;
}

.doorhandle {
  position: absolute;
  width: 50%;
  height: 50%;
  right: 0px;
  bottom: 0px;
  cursor: pointer;
}